.sectionTitle {
  display: flex;
  align-items: center;
  margin-left: 5vw;
  font-size: 25px;
  font-family: "Krona One", sans-serif;
}

.sectionTitle::after {
  content: "";
  flex: 0.1;
  margin-left: 1rem;
  height: 1px;
  width: 10px;
  background-color: var(--font);
  opacity: 20%;
}

.subTitle {
  text-transform: uppercase;
}

.gallery-item {
  flex: 1 0 20rem;
  margin: 1rem;
  overflow: hidden;
  position: relative;
  width: 422px;
  height: 308px;
}

.gallery-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: opacity 0.4s cubic-bezier(0.22, 0.61, 0.36, 1),
    0.6s transform cubic-bezier(0.18, 0.79, 0.48, 0.99);
  background-color: var(--background);
  border: solid 1px var(--elements);
}

.gallery-item:hover .overlay {
  opacity: 1;
  cursor: pointer;
}
.number {
  font-family: "Krona One", sans-serif;
  font-size: 150px;
  line-height: 0.85em;
  font-weight: 400;
  letter-spacing: 0.04em;
  display: block;
  opacity: 20%;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #ffffff;
}
.titleSmall {
  color: #ffffff;
  position: relative;
  top: -120px;
  font-family: "Dosis", sans-serif;
  font-size: 24px;
  white-space: nowrap;
}

.pieceTitle {
  position: relative;
  top: -40px;
  left: 10px;
  font-size: 40px;
  font-family: "Krona One", sans-serif;
  color: #fff;
}

/*Project CSS*/

.projectMain {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  background: var(--background);
  color: var(--font);
  letter-spacing: 1px;
  font-size: 15px;
  padding-top: 80px;
  animation: transitionIn 1s;
}

.projectWrapper {
  margin-top: 10%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.imageWrapper img {
  width: 40vw;
  height: 50vh;
}

.projectDescription {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.uxTitle {
  position: relative;
  font-family: "Krona One", sans-serif;
  font-size: 40px;
  color: var(--font);
  margin-right: 20px;
}

.projectTitle {
  font-style: italic;
  font-weight: 100;
  font-size: 20px;
  margin-right: 20px;
}

.separator {
  margin-right: 20px;
}
.tagList {
  display: flex;
  color: var(--elements);
  justify-content: right;
  list-style: none;
  margin-top: 10%;
}

.textDesc {
  display: flex;
  flex-direction: row;
}

.textDesc,
.subText {
  width: 40vw;
  height: auto;
  letter-spacing: 1px;
  line-height: 30px;
  margin-left: 5vw;
}

.subText {
  color: var(--elements);
}

.imageBig {
  position: absolute;
  top: 30%;
  right: 10%;
  height: auto;
  width: auto;
}

.detailedDescription {
  height: 100vh;
  margin-top: 10%;
  margin-left: 10vw;
}
.visualIdentity {
  margin-top: 25%;
  margin-left: 5vw;
  margin-bottom: 5%;
  height: 100vh;
}
.visualIdentity .subSection {
  margin-left: 5vw;
}

.contentVisual {
  display: flex;
}

.left,
.right {
  display: flex;
  width: 40vw;
  flex-direction: column;
  line-height: 80px;
}
.left {
  padding-left: 15%;
}

.headerFont {
  font-size: 55px;
  font-weight: bold;
}

.subFont {
  font-size: 45px;
  font-weight: 500;
}
.normalFont {
  font-size: 35px;
  font-weight: 100;
}
button {
  width: 270px;
  height: 64px;
  border: none;
  border-radius: 2px;
  margin-bottom: 20px;
  font-weight: 600;
  color: #202020;
  text-align: left;
  padding-left: 20px;
}

.linkClass {
  position: relative;
  color: var(--font);
  text-decoration: none;
  font-size: 20px;
  right: 10px;
}
.uxDetails {
  margin-left: 5vw;
}

.toolsList {
  display: inline-block;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
}

.toolsList li {
  margin-top: 10px;
}

.toolsList > li::before {
  content: "▸";
  position: relative;
  left: -5px;
  color: var(--elements);
  line-height: 12px;
}

.linkClass:hover {
  color: var(--elements);
}

.linkClass svg {
  margin-left: 15px;
  margin-right: 15px;
}

.navigationLinks {
  display: flex;
  width: 80vw;
  margin-left: 5vw;
  margin-top: 10%;

  flex-direction: row;
  justify-content: space-between;
}

.imageFull {
  margin-left: 15%;
  margin-top: 5%;
  height: 645px;
  width: auto;
}

@keyframes transitionIn {
  from {
    opacity: 0;
    transform: rotateX(-10deg);
  }
  to {
    opacity: 1;
    transform: rotateX(0);
  }
}

@media screen and (max-width: 769px) {
  .sectionTitle {
    display: flex;
    align-items: center;
    margin-left: 5vw;
    font-size: 15px;
    font-family: "Krona One", sans-serif;
  }

  .gallery-item {
    flex: 1 0 15rem;
    margin: 1rem -5vw;
    overflow: hidden;
    position: relative;
    width: 322px;
    height: 308px;
  }
  .overlay .textPhoto {
    display: flex;
    flex-direction: column;
    width: 322px;
  }

  /*project layout*/
  .projectMain {
    overflow-y: scroll;
  }

  .projectWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .imageWrapper img {
    width: 100vw;
    height: 50vh;
    padding: 5%;
  }
  .detailedDescription {
    display: flex;
    flex-direction: column;
    margin-top: 400px;
  }
  .detailedDescription .subText {
    width: 80vw;
  }
  .detailedDescription .textDesc {
    width: 90%;
  }
  .imageBig {
    width: 250px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
  }
  .imageFull {
    height: auto;
    width: 90vw;
    margin-left: 0;
  }

  .visualIdentity {
    margin-top: 50%;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  .visualIdentity .left {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 10%;
    padding-left: 5%;
  }
  .visualIdentity .right {
    position: absolute;
    top: 55%;
    left: 0;
    margin-top: 10%;
    padding-left: 5%;
  }

  .navigationLinks {
    position: absolute;
    top: 140%;
    padding-bottom: 10%;
  }
  .headerFont {
    width: min-content;
    line-height: 40px;
    font-size: 35px;
  }
  .subFont {
    width: min-content;
    line-height: 30px;
    font-size: 25px;
    margin-top: 20px;
  }
  .normalFont {
    width: min-content;
    line-height: 20px;
    font-size: 15px;
    margin-top: 20px;
  }
  .uxTitle {
    font-size: 30px;
  }
}

@media screen and (min-width: 500px) and (max-width: 1000px) {
  .imageWrapper img {
    width: 40vw;
    height: 25vh;
  }
  .imageBig {
    top: 20%;
    left: 60%;
  }
  .imageFull {
    width: 90%;
    height: auto;
    margin: 0;
    margin-top: 30%;
  }
  .left {
    margin: 0;
    padding-left: 5%;
  }
  .right {
    padding-left: 5%;
  }
}
