.footerMain {
  width: 100%;
  background: var(--background);
  color: #dee2e6;
  margin-left: 5vw;
}

.contentWraper {
  display: flex;
  align-items: center;
}

.logo {
  margin: 120px;
}

.contactWork {
  display: flex;
  flex-direction: column;
  height: 150px;
  width: 200px;
  position: relative;
}

#aboutContact {
  display: inline-block;
  list-style: none;
  text-align: left;
}
#aboutContact > li {
  margin-bottom: 10px;
  letter-spacing: 1px;
}
#socialContact {
  display: flex;
  flex-direction: row;
  list-style: none;
}

@media screen and (max-width: 900px) {
  .contentWraper {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  #aboutContact {
    text-align: center;
  }
  .logo {
    margin: 0px;
  }
}
